              linker: 'ren',
              phone: '12345678901',
              tag: 'sjisisis'
            }]
	      }
	    },
	    methods: {
        search() {
          this.getAllUsers()
        },
        selectRow(row, event, column) {
          console.log(JSON.stringify(row));
        },
        getAllUsers() {
          this.loading = true
          this.apiGet('users?keywords=' + this.keywords + '&page=' + this.currentPage).then((res) => {
            this.loading = false
            if (res.code == 200) {
              this.tableData = res.data.list
              this.dataCount = res.data.dataCount
            } else {
              _g.dealError(this, res)
            }
          })
        },
		    handleIconClick(ev) {
		      console.log(ev);
		    },
        handleSizeChange(val) {
          console.log(` ${val} `);
        },
        handleCurrentChange(val) {
          this.currentPage = val;
          console.log(`: ${val}`);
        }
		}
	}
</script>


