<template>
	<div>
    <el-row>
    <div class="m-b-20 ovf-hd">
      <div class="fr w-800 m-l-30">
      	<el-row type='flex' justify='end' :gutter='10'>
			<el-col :span='20'>
				<el-select v-model="status_value" placeholder="">
				    <el-option
				      v-for="item in status_options"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
			</el-col>
			<el-col :span='20'>
				<el-select v-model="coop_value" placeholder="">
				    <el-option
				      v-for="item in coop_options"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
			</el-col>
			<el-col>
				<el-input placeholder="" v-model="keywords">
			        <el-button slot="append" icon="search" @click="search()"></el-button>
			    </el-input>
			</el-col>
      	</el-row>
      	
        
      </div>
    </div>
    </el-row>
		<el-table
	      :data="tableData"
	      style="width: 100%">
	      <el-table-column
	        prop="num"
	        label=""
	        width="100">
	      </el-table-column>
	      <el-table-column
	        prop="title"
	        label=""
	        width="180">
	      </el-table-column>
	      <el-table-column
	        prop="pattern"
	        label=""
	        width="180">
	      </el-table-column>
	      <el-table-column
	        prop="sum"
	        label=""
	        width="80">
	      </el-table-column>
	      <el-table-column
	        prop="client"
	        label=""
	        width="150">
	      </el-table-column>
	      <el-table-column
	        prop="servicer"
	        label="">
	      </el-table-column>
        <el-table-column
          prop="create_time"
          label="">
        </el-table-column>
        <el-table-column
          prop="status"
          label="">
        </el-table-column>
        <el-table-column
          prop="mark_status"
          label="">
        </el-table-column>
	  </el-table>
     <div class="pos-rel p-t-20">
      <div class="block pages">
        <el-pagination
        @current-change="handleCurrentChange"
        layout="prev, pager, next, jumper"
        :page-size="15"
        :current-page="currentPage"
        :total="dataCount">
        </el-pagination>
      </div>
    </div>
	</div>
</template>
<style type="text/css">
  .page-part{
    margin-top: 10px;
  }
</style>

<script>
	export default{
	    data() {
	      return {
          currentPage: 1,
          dataCount: 1,
	        keywords: '',
	        tableData: [
	        {
	            client: '',
	            create_time: '2016-01-01',
              	mark_status: '',
              	status:'',
              	pattern: '',
	            servicer: 'ren',
	            sum:'34344',
              	title: '12345678901',
              	num: 'sjisisis'
	        },
            {
                client: '',
	            create_time: '2016-01-01',
              	mark_status: '',
              	status:'',
              	pattern: '',
	            servicer: 'ren',
	            sum:'34344',
              	title: '12345678901',
              	num: 'sjisisis'
            }],
            status_options: [{
	          value: '1',
	          label: ''
	        }, {
	          value: '2',
	          label: ''
	        }, {
	          value: '3',
	          label: ''
	        }, {
	          value: '4',
	          label: ''
	        }, {
	          value: '5',
	          label: ''
	        }],
			coop_options: [{
	          value: '1',
	          label: ''
	        }, {
	          value: '2',
	          label: ''
	        }, {
	          value: '3',
	          label: ''
	        }, {
	          value: '4',
	          label: ''
	        }, {
	          value: '5',
	          label: ''
	        }],
	        coop_value: '',
        	status_value: ''
	      }
	    },
	    methods: {
        search() {
          this.getAllUsers()
        },
        getAllUsers() {
          this.loading = true
          this.apiGet('users?keywords=' + this.keywords + '&page=' + this.currentPage).then((res) => {
            this.loading = false
            if (res.code == 200) {
              this.tableData = res.data.list
              this.dataCount = res.data.dataCount
            } else {
              _g.dealError(this, res)
            }
          })
        },
	    handleIconClick(ev) {
	      console.log(ev);
	    },
        handleSizeChange(val) {
          console.log(` ${val} `);
        },
        handleCurrentChange(val) {
          this.currentPage = val;
          console.log(`: ${val}`);
        }
		}
	}
</script>


